@keyframes spin {
  from {
    rotate: 0deg;
  }
  to {
    rotate: 360deg;
  }
}
.cursor {
  position: absolute;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  z-index: 9999;
  backdrop-filter: blur(2px) invert(100%);
  transition: all 200ms cubic-bezier(0.16, 1, 0.3, 1);
  transition-property: width, height, border-radius;
}
.cursor svg use {
  width: 50px;
  height: 50px;
}
.cursor svg #circle {
  animation: 2s rotate 0s linear infinite;
}